.experiences {
    .experiences-content {
            margin-top: 2rem;
            line-height: 24px;
            
        .experiences-content-exp1, .experiences-content-exp2 {
            h4 {
                padding: 10px;
                color: $color-h3-bottle;
                font-family: $fnt-opensans;
                font-size: 14px;
                letter-spacing: 4px;
                text-transform: uppercase;
            }
            h5 {
                color: $color-h1;
                font-family: $fnt-opensans;
                font-size: 1rem;
                text-transform: capitalize;
                padding-left: 1rem;
            }
            span {
                font-family: $fnt-opensans;
                color: $color-h1;
                font-style: italic;
                font-size: 0.7rem;
            }
            p {
                font-family: $fnt-opensans;
                line-height: 1.5rem;
                font-weight: 100;
                color: $color-h1;
                margin: .5rem;
                font-size: 1rem;
                text-align: left;
                margin-bottom: 2rem;
                

                a {
                    color: $color-btn;
                }
            }
        }
         
    }
}